	module.exports = {"data":{"markdownRemark":{"html":"<p>I've been reading an excellent report from <a href=\"http://www.eldis.org/\">Eldis </a>(an incredible clearinghouse of development information) about the implementation of ICT programs in developing countries. In part the report seeks to question some conventional wisdom about the necessity of trendy technologies. On the whole it is a great synopsis of practical research with a clearheaded focus on poverty elimination. I recommend the <a href=\"http://www.eldis.org/fulltext/sidaictpoverty.pdf\">full text pdf</a>, and here's a bit from the executive report: </p>\n<blockquote class=\"large\">\n<img src='/static/images/PanosTBolstadradio.jpg' alt='Man with radio' />\n<p>This report reviews the evidence on how (or if) ICTs should be used in support of poverty reduction exercises.</p>\n<p>There is one characteristic that is common to most of the ICT-related poverty alleviation programs. It finds that the most effective ICTs used are typically basic ones‚ telephone and radio are most common, and when computers or the Internet are involved, they are for restricted, targeted uses.</p>\n<p>It finds several common characteristics of successful projects:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\">* the focus is on poverty alleviation and not on ICT use\n* ICT components are kept as simple as practical\n* ICT practitioners are involved in the design of the ICT components\n* there is significant community involvement\n* there is a focus on training to ensure success and sustainability\n* there is consideration of a plan for success; how to replicate and scale project if it is successful&lt;/blockquote&gt;</code></pre>\n      </div>\n<p>View the original page: <a href=\"http://www.eldis.org/cf/search/disp/DocDisplay.cfm?Doc=DOC20186&#038;Resource=f1ict\">Eldis - ICT for Development</a></p>","frontmatter":{"created_at":"December 15, 2005","path":"/2005/12/15/combatting-poverty-and-ict-fads","title":"Combatting Poverty (and ICT fads)","tags":["design","technology","ict4d"]}}},"pathContext":{}}

